<template>
  <div id="app">
    <div class="menu"> 
      <a v-for="menu in menus" :key="menu" href="#" @click="menu === 'Event' ? toggleDiscountBanner() : null">{{ menu }}</a>
    </div>

    <transition name="fade">
      <DiscountBanner v-if="showDiscountBanner" />
    </transition>

    <hr>

    <button @click="sortPriceAsc">Í∞ÄÍ≤© Ïò§Î¶ÑÏ∞®Ïàú Ï†ïÎ†¨</button>
    &nbsp;
    <button @click="sortPriceDesc">Í∞ÄÍ≤© ÎÇ¥Î¶ºÏ∞®Ïàú Ï†ïÎ†¨</button>
    &nbsp;
    <button @click="sortTitleAsc">Ïù¥Î¶Ñ Ïò§Î¶ÑÏ∞®Ïàú Ï†ïÎ†¨</button>
    &nbsp;
    <button @click="sortTitleDesc">Ïù¥Î¶Ñ ÎÇ¥Î¶ºÏ∞®Ïàú Ï†ïÎ†¨</button>
    &nbsp;
    <button @click="resetSort">ÏõêÎûòÎåÄÎ°ú</button>

    <transition name="fade">
      <RoomModal 
        v-if="modalStatus"
        :rooms-info="roomsInfo" 
        :current-product-no="currentProductNo" 
        :modal-status="modalStatus" 
        :report-counts="reportCounts" 
        @close="closeModal" 
        @reset-report-count="resetReportCount"
      />
    </transition>
    
    <div v-for="(room, index) in roomsInfo" :key="room.title">
      <img :src="room.image" alt="Room Image" class="room-img">
      <h4 @click="openModal(index)">{{ room.title }}</h4>
      <p>{{ room.price }}Ïõê</p>
      <button @click="reportCounts[index]++">ÌóàÏúÑÎß§Î¨º Ïã†Í≥†</button>
      <span>Ïã†Í≥†Ïàò: {{ reportCounts[index] }}</span>
    </div>
  </div>
</template>

<script>
import roomsInfo from "./assets/oneroom.js";
import RoomModal from "./components/RoomModal.vue";
import DiscountBanner from "./components/DiscountBanner.vue";

export default {
  name: 'App',
  data() {
    return {
      modalStatus: false,
      currentProductNo: 0,
      roomsInfo: [...roomsInfo],
      originalRoomsInfo: [...roomsInfo], 
      menus: ['Home', 'Shop', 'Event'],
      reportCounts: new Array(roomsInfo.length).fill(0),
      showDiscountBanner: false
    }
  },
  mounted() {
    this.sortTest();
  },
  methods: {
    sortTest() {
      // Ïà´Ïûê Ï†ïÎ†¨ ÌÖåÏä§Ìä∏
      let array = [3, 5, 2];
      array.sort((a, b) => a - b);
      console.log(array);
    },
    sortPriceAsc() {
      this.roomsInfo.sort((a, b) => a.price - b.price);
    },
    sortPriceDesc() {
      this.roomsInfo.sort((a, b) => b.price - a.price);
    },
    sortTitleAsc() {
      this.roomsInfo.sort((a, b) => a.title.localeCompare(b.title));
    },
    sortTitleDesc() {
      this.roomsInfo.sort((a, b) => b.title.localeCompare(a.title));
    },
    resetSort() {
      this.roomsInfo = [...this.originalRoomsInfo];
    },
    openModal(index) {
      this.currentProductNo = index;
      this.modalStatus = true;
    },
    closeModal() {
      this.modalStatus = false;
    },
    resetReportCount(index) {
      this.reportCounts[index] = 0;
    },
    toggleDiscountBanner() {
      this.showDiscountBanner = !this.showDiscountBanner;
    }
  },
  components: {
    RoomModal,
    DiscountBanner
  }
}
</script>

