<template>
  <div class="black-bg" v-if="modalStatus">
    <div class="white-bg">
      <img :src="roomsInfo[currentProductNo].image" alt="Room Image">
      <h4>{{ roomsInfo[currentProductNo].title }}</h4>
      <p>{{ roomsInfo[currentProductNo].content }}</p>
      <p>{{ calculatedPrice }}Ïõê</p>
      <div>
        <label for="months">Í∞úÏõî Ïàò ÏÑ†ÌÉù:</label>
        <input type="number" id="months" v-model.number="selectedMonths" min="1">
      </div>
      <div>
        <p>Ïù¥ Î¨ºÍ±¥Ïùò ÌóàÏúÑÎß§Î¨ºÏã†Í≥† Í±¥Ïàò: {{ reportCounts[currentProductNo] }}</p>
        <button @click="resetReportCount">Ïã†Í≥† Í±¥Ïàò Ï¥àÍ∏∞Ìôî</button>
      </div>
      
      
      <DiscountBanner v-if="showDiscountBanner" :countdown="countdown" />
      
      <button @click="$emit('close')">Îã´Í∏∞</button>
    </div>
  </div>
</template>

<script>
import DiscountBanner from "./DiscountBanner.vue";

export default {
  name: "RoomModal",
  props: {
    roomsInfo: Array,
    currentProductNo: Number,
    modalStatus: Boolean,
    reportCounts: Array,
  },
  data() {
    return {
      selectedMonths: 3,
      showDiscountBanner: false,
      countdown: 10,
      countdownInterval: null 
    };
  },
  computed: {
    calculatedPrice() {
      return this.roomsInfo[this.currentProductNo].price * this.selectedMonths;
    },
  },
  methods: {
    resetReportCount() {
      this.$emit('reset-report-count', this.currentProductNo);
    },
    startCountdown() {
      this.showDiscountBanner = true;
      this.countdownInterval = setInterval(() => {
        if (this.countdown > 0) {
          this.countdown--;
        } else {
          clearInterval(this.countdownInterval);
          this.showDiscountBanner = false;
        }
      }, 1000); 
    },
    validateMonths() {
      if (this.selectedMonths < 3) {
        alert("ÎÑàÎ¨¥ ÏßßÏïÑÏöî! 3Í∞úÏõî Ïù¥ÏÉÅ ÏÑ†ÌÉùÌï¥Ï£ºÏÑ∏Ïöî.");
        this.selectedMonths = 3;
      }
    }
  },
  mounted() {
    
    if (this.modalStatus) {
      this.startCountdown();
    }
  },
  watch: {
    modalStatus(newVal) {
      if (newVal) {
        this.startCountdown();
      } else {
        clearInterval(this.countdownInterval); 
      }
    },
    selectedMonths() {
      this.validateMonths();
    }
  },
  components: {
    DiscountBanner,
  },
}
</script>

<style scoped>
.black-bg {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.white-bg {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  width: 300px;
}

.white-bg h4, .white-bg p {
  margin: 10px 0;
}

.white-bg img {
  max-width: 100%;
  height: auto;
}
</style>
